@tailwind base;
@tailwind components;
@tailwind utilities;

body {
    @apply text-white;
    @apply bg-slate-900
}

.privacy-policy {
    @apply p-8 bg-white text-black max-w-3xl mx-auto my-4 rounded-lg;
}

.privacy-policy ul { 
    @apply list-disc mx-6 my-2;
}

.privacy-policy .heading {
    @apply font-heading text-4xl underline block text-center mb-4;
}

.privacy-policy strong {
    @apply font-heading text-xl block mb-2;
}

.privacy-policy footer {
    @apply mt-4;
}

.privacy-policy a {
    @apply text-blue-950 underline;
}

